import React,{useEffect} from 'react'
import Image from '../assets/images/image 12.png';
import Grid from '@material-ui/core/Grid'
import Box from '@material-ui/core/Box'
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton'
import Button from '@material-ui/core/Button'
import DeleteIcon from '@material-ui/icons/Delete'
import Delete from '../assets/images/bin.png'
import {Link} from "react-router-dom"
import {userServices} from '../_userServices/userServices'
import Loading from '../Loader/loader'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      '& > *': {
        margin: theme.spacing(1),
      },
    },
    input: {
      display: 'none',
    },
  }),
);

  
export default function Photos(props) {
    const classes = useStyles();
    const [state,setState] = React.useState({
        image:"",
        loading:false
    })
 const [images,setImage]=React.useState({
     images:"",
 })
    useEffect(() => {
        setState({...state,loading:true})
        userServices
        .images(props.id)
        .then((res)=>{
        setState({...state, image : res.data.service_images,loading:false})
      })
      
      },[]);
      const onClick=(img)=>{
        userServices
        .deleteImage(props.id,img)
        .then((res)=>{
        const data = state.image.filter((value)=>(
            value !== img 
        ))
        setState({...state,image:data})
        })
      }
    
      const handleCapture = ({ target }) => {
        const fileReader = new FileReader();
        const name = target.accept.includes('image') ? 'images' : 'videos';
        fileReader.readAsDataURL(target.files[0]);
        
        fileReader.onload = (e) => {
            
           setState({...state,loading:true})
    userServices
    .uploadImage(props.id, e.target.result,target.value)
    .then((res)=>{
        setImage({...state,images:""})
        userServices
        .images(props.id)
        .then((res)=>{
        setState({...state, image : res.data.service_images,loading:false})
      })
    })
            
                   
        };
    }

    
const onSave=()=>{
    setState({...state,loading:true})
    userServices
    .uploadImage(props.id, images.images,images.imageName)
    .then((res)=>{
        setImage({...state,images:""})
        userServices
        .images(props.id)
        .then((res)=>{
        setState({...state, image : res.data.service_images,loading:false})
      })
    })
}
    return (
        <div>
            <Box fontSize={16} color="#4A4A4A" className="font-family-bold" mb={1}>Photos</Box>
            <Grid container>
                <Grid item xl={4} lg={4} md={4} sm={6} xs={12}>
                    <Grid container spacing={2}>
                        {
                            state.image === "" ? "" :state.image.map((value,index)=>(
                        <Grid item lg={4} md={4} sm={6} xs={6}>
                            <img src={`http://staging.sportido.pro${value}`} width="100%"/>
                            <Grid container >
                                <Grid item lg={9} md={10} sm={9} xs={9}>
                            <Box pl={1} mb={2} className="font-family-regular" color="#4A4A4A" fontSize={12} border="1px solid #DADADA">{index + 1}</Box>
                                    </Grid>
                                    <Grid item lg={3} md={2} sm={3} xs={3}>
                                        <Box mt={1.3}>
                                    <IconButton onClick={onClick.bind(this,value)} className="pull-right" aria-label="delete" size="small">
                                    <img src={Delete} width="16px" />
                                    </IconButton>   
                                    </Box>    
                                    </Grid>
                                </Grid>
                        </Grid>
                            ))
                        }
                        
                    </Grid>
                    <div className={classes.root}>      
        <input
        accept="image/*"
        className={classes.input}
        id="contained-button-file"
        onChange={handleCapture}
        type="file"
      />
      {
          
          images.images === "" ?  <label htmlFor="contained-button-file">
          <Button className="btn-primary" component="span">
              <Box px={3}>Add Photo</Box>
            </Button>
          </label>:
            <Button onClick={onSave} className="btn-primary" component="span">
            <Box px={3}>Upload Image</Box>
          </Button>
      }
     
      
    
      </div>
                </Grid>
            </Grid>
            {state.loading === true ? <Loading /> :""}
            <Box fontSize={10} color="#4A4A4A" mt={3} className="font-family-regular" mb={2}> Important</Box>
                    <Box fontSize={10} color="#4A4A4A" className="font-family-regular" mb={0.2}>1) Compress images so that the size <span className="font-family-bold">10kb to 60kb</span></Box>
                    <Box fontSize={10} color="#4A4A4A" className="font-family-regular" mb={0.2}>2) <Link to="">www.compressimage.toolur.com</Link>- Use this to compress the image</Box>
                    <Box fontSize={10} color="#4A4A4A" className="font-family-regular" mb={0.2}>3) Select <span className="font-family-bold">Type c</span> compression</Box>
                    <Box fontSize={10} color="#4A4A4A" className="font-family-regular" mb={0.2}>4) Compress <span className="font-family-bold">upto 60%</span> as per image quality</Box>
                    <Box fontSize={10} color="#4A4A4A" className="font-family-regular" mb={0.2}>5) Retain the <span className="font-family-bold">aspect ratio</span>, eg. compress a 6000*6000 image to 100*100</Box>
        </div>
    )
}
