import React, { Component } from 'react'
import clsx from 'clsx';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import PropTypes from 'prop-types';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Container,Box,FormControlLabel } from '@material-ui/core';
import Order from '../assets/images/order.png'
import Filter from '../assets/images/filter.png'
import Search from '../assets/images/Search.png'
import Whatsapp from '../assets/images/whatsapp.png'
import {Link} from 'react-router-dom';
import Switch from '@material-ui/core/Switch';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Drawer from '@material-ui/core/Drawer';
import Logo from '../assets/images/logo.png'
import Admin from '../assets/images/admin.png'
import List from '@material-ui/core/List';
import InboxIcon from '@material-ui/icons/Inbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import SearchAutocomplete from '../_commen/searchService'
import Dialog from '@material-ui/core/Dialog';
import Divider from '@material-ui/core/Divider';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import Grid from '@material-ui/core/Grid';
import {userServices} from '../_userServices/userServices'
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginTop:15,
  },
  title: {
    flexGrow: 1,
  },
  appPadding:{
      paddingRight:0,
  },
  iconBtn:{
      marginRight:10,
  },
  appbar:{
    flexGrow: 1,
      borderRadius:4,
      boxShadow:"0px 0px 7px rgba(0, 0, 0, 0.05)",
  },
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
  navWidth:{
      width:"100%",
  },
  logout:{
      position:'absolute',
      bottom:20,
      fontSize:12,
      textTransform:'capitalize',
      fontFamily:'Montserrat-Regular!important'
  },
  appBar: {
    position: 'relative',
    width:"100%",
  },
  searchbar:{
    minWidth:"240px"
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });
const IOSSwitch = withStyles((theme) => ({
    root: {
      width: 42,
      height: 26,
      padding: 0,
      borderRadius:62,
      margin: theme.spacing(1),
    },
    switchBase: {
      padding: 1,
      '&$checked': {
        transform: 'translateX(16px)',
        color: theme.palette.common.white,
        '& + $track': {
          backgroundColor: '#98C28A',
          opacity: 1,
          border: 'none',
        },
      },
      '&$focusVisible $thumb': {
        color: '#98C28A',
        border: '6px solid #fff',
      },
    },
    
    thumb: {
      width: 24,
      height: 24,
    },
    track: {
      borderRadius: 26 / 2,
      border: `1px solid ${theme.palette.grey[400]}`,
    //   backgroundColor: theme.palette.grey[50],
      backgroundColor:"#C28D8A",
      opacity: 1,
      transition: theme.transitions.create(['background-color', 'border']),
    },
    checked: {},
    focusVisible: {},
  }))(({ classes, ...props }) => {
    return (
      <Switch
        focusVisibleClassName={classes.focusVisible}
        disableRipple
        classes={{
          root: classes.root,
          switchBase: classes.switchBase,
          thumb: classes.thumb,
          track: classes.track,
          checked: classes.checked,
        }}
        {...props}
      />
    );
  });

const Listings=(props)=> {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
  });
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const [selectedIndex, setSelectedIndex] = React.useState(1);

  const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
    if(index === 2){
      props.props.history.push("/marketing")
    }
    if(index === 3){
      props.props.history.push("/reports")
    }
  };
  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  if(localStorage.getItem("status") !== "success"){
    props.props.history.push('./login')
  }
  const Logout =()=>{
    localStorage.removeItem("status")
    props.props.history.push("/login")
  }
  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onKeyDown={toggleDrawer(anchor, false)}
    >
<Box pl={2} pt={2} pb={1}>
    <img src={Logo} width={120}/>
    <div>
        <img src={Admin} width={70}/>
    </div>
</Box>
<Box pt={2} px={2} className="body-bg mob-sidenav">
 <List component="nav" aria-label="main mailbox folders">
        <ListItem
          button
          selected={selectedIndex === 0}
          onClick={(event) => handleListItemClick(event, 0)}
        >
         
          <ListItemText primary="My Follow-ups" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 1}
          onClick={(event) => handleListItemClick(event, 1)}
        >
          <ListItemText primary="Services" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 2}
          onClick={(event) => handleListItemClick(event, 2)}
        >
          <ListItemText primary="Marketing" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 3}
          onClick={(event) => handleListItemClick(event, 3)}
        >
          <ListItemText primary="Reports" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 4}
          onClick={(event) => handleListItemClick(event, 4)}
        >
          <ListItemText primary="Payouts" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 5}
          onClick={(event) => handleListItemClick(event, 5)}
        >
          <ListItemText primary="Users" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
      </List>
    
      <Button className={classes.logout} onClick={Logout}>Logout</Button>
      </Box>
    </div>
  );
  return (
      <div className="body-bg">
    <div className={classes.root}>
        <Container>
      <AppBar position="static" color="inherit" className={classes.appbar}>
        <Toolbar className={classes.appPadding}>
          <IconButton edge="start" onClick={toggleDrawer('left', true)} className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
          Services
          </Typography>
          <IconButton onClick={handleClickOpen} className={classes.iconBtn} aria-label="delete" size="small">
          <img src={Search} width="16px"/>
        </IconButton>
          <IconButton className={classes.iconBtn} aria-label="delete" size="small">
          <img src={Filter} width="16px"/>
        </IconButton>
        <IconButton className={classes.iconBtn} aria-label="delete" size="small">
          <img src={Order} width="16px"/>
        </IconButton>
        </Toolbar>
      </AppBar>
      </Container>
    </div>
    <Box my={2} />
    <Container>
        <Box p={2} boxShadow="0px 6px 25px rgba(0, 0, 0, 0.08)" bgcolor="#fff" borderRadius={6}>
            {
                props.data.length === 0  ? "" : props.data.map((value)=>(
                    <>
            <Box fontSize={12} className="font-family-bold">{value.provider_name}<Link className="float-right" to=""><img src={Whatsapp} width={14}/></Link></Box>
            <Box fontSize={12} mt={1} className="font-family-regular">{value.service_name} <span className="float-right">{value.sync_type}</span></Box>
            <Box fontSize={12} mt={1} className="font-family-regular"><FormControlLabel className="form-margin"
        control={<IOSSwitch name="checkedB" checked={value.is_active === 1 ? true : false}/>}
                /> Not Visible <span className="float-right padding2">{value.last_hit_sync === null ? "-" : value.last_hit_sync }</span></Box>
       <Box fontSize={12} mt={1} className="font-family-regular"><FormControlLabel className="form-margin"
        control={<IOSSwitch name="checkedB" checked={value.is_partner === 1 ?true : false}/>}
      /> Not Bookable <a to="" className="float-right"><ChevronRightIcon className="" fontSize="small"/></a></Box>
      <Box my={2} border="1px solid #EFEFEF"/>
      </>
      ))
            }
        </Box>
        {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
        <div>
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
        <Container>
          <Box mt={1} />
        <Grid container>
          <Grid item xs={1}>
            <Box mt={1}/>
          <IconButton edge="start" size="small" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon fontSize="small"/>
            </IconButton>
          </Grid>
          <Grid item xs={9}> <SearchAutocomplete className={classes.searchbar} /></Grid>
          <Grid item xs={2}>
          <Box mt={1}/>
          <Button autoFocus size="small" color="inherit" onClick={handleClose}>
              save
            </Button>
          </Grid>
        </Grid>
        </Container>
        <Box borderBottom="1px solid #D7D7D7" mt={1} />
      </Dialog>
    </div>
    </Container>
    </div>
  );
}


export default class Services extends Component {
  constructor(props){
    super(props)
    this.state={
      data:[],
    }
  }
  componentDidMount=()=>{
    if(localStorage.getItem("status") !== "success"){
      this.props.history.push('login')
    }
    userServices
    .services()
    .then((res)=>{
    if(res.success === 1){
      this.setState({data:res.data.service_data})
    }
  })
  }
  render() {
    const {data} = this.state;
    return (
      <div>
       <Listings data={data} props={this.props}/>
      </div>
    )
  }
}
