import axios from "axios";
import qs from "query-string";
export const userServices = {
    login,
    services,
    visible,
    bookable,
    rename,
    Sync,
    featured,
    slotTime,
    images,
    deleteImage,
    uploadImage
}
const URL_PREFIX = "http://staging.sportido.pro";

async function login(mobile,password) {
    const options = {
      headers: { 
        "Content-Type": "application/json",
      }
    };

  const { data: response } = await axios.post(
    URL_PREFIX + "/v1/dashboard/login", 
    {
      mobile:mobile,
      password:password
    },
    options
  );
  return response;

}

async function services(state,page,search,sort) {
  
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
  console.log(search)
const { data: response } = await axios.post(
  
  URL_PREFIX + "/v1/dashboard/services", 
  {
    "sort_by": sort === undefined ? state.sort : sort, 
    "state_name": state.state,
    "sync_type": state.type,
    "is_partner":parseInt(state.bookable) ,
    "is_active": parseInt(state.visible),
    "is_featured": parseInt(state.featured),
    "keyword":search === undefined ? "" : search ,
    "page": page === undefined ? 1 : page + 1
  },
  options
);
console.log(response)
return response;

}
async function visible(id,visible) {
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
  const isVisible = visible === true ? 1 : 0 ;
const { data: response } = await axios.post(
  URL_PREFIX + "/v1/dashboard/service/visible", 
  {
    "service_id": id,
    "is_visible": isVisible,
  },
  options
);
return response;

}
