async function uploadImage(id,image,name) {
  console.log(name,name.length)
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
const { data: response } = await axios.post(
  URL_PREFIX + "/v1/dashboard/service/image/upload", 
  {
    service_id: id,
    "images":image,
    file_names:name.slice(-10),
  },
  options
);
return response;

}