import React,{useEffect} from 'react'
import Grid from '@material-ui/core/Grid'
import Box from '@material-ui/core/Box'
import TextField from '@material-ui/core/TextField'
import Radio from '@material-ui/core/Radio'
import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import RadioGroup from '@material-ui/core/RadioGroup'
import Button from '@material-ui/core/Button'
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import { DatePicker } from "@material-ui/pickers";
import {userServices} from '../_userServices/userServices'

export default function Commissions(props) {
  const [selectedDate, handleDateChange] = React.useState(new Date());
  const [state,setState] = React.useState({
    data:"",
  })
  useEffect(() => {
    userServices
    .featured(props.id)
    .then((res)=>(
      setState({...state,data:res})
    ))
  });
    return (
        <div>
            <Grid container>
                <Grid item xl={4} lg={4} md={4} sm={6} xs={12}>
                <Box fontSize={16} mb={2} className="font-family-bold">
          Featured
        </Box>
                <Box
          bgcolor="#fff"
          boxShadow="0px 1px 4px rgba(0, 0, 0, 0.08)"
          borderRadius={4}
          p={2}
          mb={2}
        >
            <Box fontSize={12} color="#4A4A4A" className="font-family-bold" mb={1}>Slot Bookings</Box>
            <FormControl component="fieldset">
        <RadioGroup row aria-label="position" value={state.data === "" ? "no" : state.data.data.is_featured === 1 ? "yes" : "no" } name="position" defaultValue="top">
          <FormControlLabel
            value="yes"
            control={<Radio color="primary" />}
            label="Yes"
            labelPlacement="end"
          />
          <FormControlLabel
            value="no"
            control={<Radio color="primary" />}
            label="No"
            labelPlacement="end"
          />
        </RadioGroup>
      </FormControl>
      {state.data === "" ? "" : state.data.data.is_featured === 0 ? "" : 
      <>
      <Box
           border="1px solid #DADADA"
           className="input-oulined date-picker" 
           pt={0.8}
           mb={2}
           borderRadius={4}
           bgcolor="#fff"
           fontSize={9}
         >
           <span>Start Date</span>
          <Box >
            <MuiPickersUtilsProvider utils={MomentUtils}>
                  <DatePicker
                  readOnly
                  onChange={handleDateChange}
                  animateYearScrolling
                />
                
              </MuiPickersUtilsProvider>
              </Box>
         </Box>
         <Box
           border="1px solid #DADADA"
           className="input-oulined date-picker" 
           pt={0.8}
           
           borderRadius={4}
           bgcolor="#fff"
           fontSize={9}
         >
           <span>End Date</span>
          <Box >
            <MuiPickersUtilsProvider utils={MomentUtils}>
                  <DatePicker
                  value={selectedDate}
                  onChange={handleDateChange}
                  animateYearScrolling
                />
                
              </MuiPickersUtilsProvider>
              </Box>
         </Box>
         </>}
    </Box>

                </Grid>
            </Grid>
            <Button className="btn-primary" >
          <Box px={3}>Save</Box>
        </Button>
        </div>
    )
}
